/* Background styling */
body {
  margin: 0;
  font-family: sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(to bottom, #1e293b, #111827);
  color: white;
}

/* Digital Clock Styling */
.clock-container {
  background-color: #1f2937;
  padding: 3rem;
  border-radius: 1rem;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.6);
}

.text-glow {
  color: #10b981;
  text-shadow: 0px 0px 8px #10b981;
}
.text{
  margin-left: 100px;
}

/* Analog Clock Styling */
.analog-clock {
  position: relative;
  width: 16rem;  /* Adjust size as needed */
  height: 16rem;
  background-color: #1e293b;
  border-radius: 50%;
  border: 8px solid #374151;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.6);
  margin-top: 1rem;
}

/* Clock Hands */
.hour-hand,
.minute-hand,
.second-hand {
  position: absolute;
  bottom: 50%;
  transform-origin: bottom;
  transform: rotate(0deg);
}

.hour-hand {
  width: 4px;
  height: 20%;
  background-color: #10b981;  /* Green for hour */
  border-radius: 4px;
}

.minute-hand {
  width: 3px;
  height: 30%;
  background-color: #60a5fa;  /* Blue for minute */
  border-radius: 3px;
}

.second-hand {
  width: 2px;
  height: 35%;
  background-color: #ef4444;  /* Red for second */
  border-radius: 2px;
}

/* Center Pin */
.center-pin {
  position: absolute;
  width: 6px;
  height: 6px;
  background-color: white;
  border-radius: 50%;
  z-index: 10;
}

/* Clock Ticks */
.clock-tick {
  position: absolute;
  width: 2px;
  height: 10px;
  background-color: #9ca3af;
  transform-origin: top;
}
